import React, {memo, Fragment} from "react";
import { makeStyles } from "@material-ui/core/styles";
// Material UI components
import { Box } from "@material-ui/core";
import MoveIcon from "@material-ui/icons/UnfoldMore";
import lightGreen from "@material-ui/core/colors/lightGreen";
// App custom
import CustomTheme from "./Theme";

const componentStyleOverrides = {
  box: {
    display: "flex",
    justifyContent: "flex-end",
    padding: `5px ${CustomTheme.spacing(2)}px`,
    backgroundColor: lightGreen[200],
    borderRadius: CustomTheme.shape.borderRadius,
    border: `1px solid ${lightGreen[400]}`,
    ...CustomTheme.typography.button
  },
  icon: {
    marginRight: CustomTheme.spacing(2)
  },
};
const useStyles = makeStyles(componentStyleOverrides);

function DragPreview(props) {
  const { type } = props;
  const classes = useStyles();
  const questionTypes = [
    {
      name: "Short Answer",
      type: "ShortAnswer",
    },
    {
      name: "Paragraph",
      type: "Paragraph",
    },
    {
      name: "Multiple Choice",
      type: "MultipleChoice",
    },
    {
      name: "Checkboxes",
      type: "Checkboxes",
    }
  ];
  const thePreview = questionTypes.map((question, index) => {
    let preview = null;

    if (question.type === type) {
      preview = <Fragment key={index}><MoveIcon className={classes.icon} />{question.name}</Fragment>;
    }

    return preview;
  });

  return (
    <Box className={classes.box}>{thePreview}</Box>
  );
}

export default memo(DragPreview);
