// React
import React, { Fragment } from "react";
import { connect } from "react-redux";
// App custom
import { getQuestions, getQuestionCount, getActive } from "../redux/selectors";
import questionTypes from "../questionTypes";
import NoMatch from "../QuestionList/NoMatch";
import NoQuestions from "../QuestionList/NoQuestions";

function renderQuestionType(question, index, active) {
  const propsBootstrap = {
    key: question.id,
    index: index,
    id: question.id,
    isActive: active === question.id ? true : false,
    core: {
      ...question
    }
  };

  switch (question.type) {
    case "ShortAnswer":
      return questionTypes.ShortAnswer.component(propsBootstrap);
    case "Paragraph":
      return questionTypes.Paragraph.component(propsBootstrap);
    case "Date":
        return questionTypes.Date.component(propsBootstrap);
    case "MultipleChoice":
      return questionTypes.MultipleChoice.component({ ...propsBootstrap });
    case "Checkboxes":
      return questionTypes.Checkboxes.component({ ...propsBootstrap });
    default:
      return <NoMatch key="nomatch" />;
  }
}

const QuestionList = ({ questions, active, count }) => {
  return (
    <Fragment>
      {questions &&
        questions.list.map((question, index) => {
          return renderQuestionType(question, index, active);
        })}
      {count === 0 && <NoQuestions />}
    </Fragment>
  );
};

const mapStateToProps = state => {
  const questions = getQuestions(state);
  const active = getActive(state);
  const count = getQuestionCount(state);

  return { questions, active, count };
};

export default connect(mapStateToProps)(QuestionList);
