import { useDrag } from "react-dnd";

function useQuestionDrag(itemType, id, index, questionType) {
  const [{ isDragging }, drag, preview] = useDrag({
    item: { type: itemType, id, index, questionType: questionType },
    collect: monitor => ({
      isDragging: monitor.isDragging()
    })
  });

  return {
    isDragging, drag, preview
  }
}

export default useQuestionDrag;
