// React
import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
// App custom
import { getAdditional, getOptions } from "../../../redux/selectors";
import Option from "./Option";
import AddOther from "./AddOther";
import Other from "./Other";

function renderOption(parentId, optionType, optionValue, optionId) {
  return (
    <Option
      type={optionType}
      value={optionValue}
      key={optionId}
      parentId={parentId}
      optionId={optionId}
    />
  );
}

const OptionsList = ({ additional, options, parentId, optionType }) => {
  if (options !== null) {
    return (
      <Fragment>
        {typeof additional[parentId].options !== "undefined" && (
          additional[parentId].options.map(option => {
            return renderOption(parentId, optionType, option.value, option.id);
          })
        )}
        {additional[parentId].hasOther && (
          <Other
            type={optionType}
            parentId={parentId}
          />
        )}
        <AddOther
          type={optionType}
          hasOther={additional[parentId].hasOther}
          parentId={parentId}
        />
      </Fragment>
    );
  } else {
    return null;
  }
};

const mapStateToProps = (state, ownProps) => {
  const { parentId } = ownProps
  const additional = getAdditional(state);
  const options = getOptions(state, parentId)

  return { additional, options };
};

OptionsList.propTypes = {
  parentId: PropTypes.string.isRequired,
  optionType: PropTypes.string.isRequired,
};

export default connect(mapStateToProps)(OptionsList);
