import CustomTheme from "../Theme";

const Styles = {
  title: {
    "& .MuiFormLabel-root": {
      fontSize: CustomTheme.typography.pxToRem(24)
    },
    "& .MuiInputBase-input": {
      fontSize: CustomTheme.typography.pxToRem(24)
    },
    "& .MuiInputLabel-asterisk": {
      color: CustomTheme.palette.error.main
    }
  },
  paper: {
    paddingLeft: CustomTheme.spacing(4),
    paddingRight: CustomTheme.spacing(4),
    borderLeft: `5px solid transparent`,
    opacity: 1,
  },
  paperActive: {
    position: "relative",
    borderLeft: `5px solid ${CustomTheme.palette.primary.main}`,
  },
  paperDragging: {
    opacity: 0,
    // When the question is being dragged, certain parts of it are hidden, so that it's height is reduced. Similar to how Google forms does it.
    // FIXME: This can cause a bug with dragging the last question and the drag preview not appearing.
    "& > form": {
      display: "none",
    },
    "& > div:nth-child(2)": {
      visibility: "hidden"
    },
    "& > div:nth-child(4)": {
      visibility: "hidden"
    }
  },
  form: {
    width: "100%" // Fix IE 11 issue.
  },
  input: {
    marginTop: CustomTheme.spacing(1),
    marginBottom: CustomTheme.spacing(1),
    "& .MuiInputBase-input": {
      fontSize: CustomTheme.typography.pxToRem(14)
    }
  },
  button: {
    marginLeft: CustomTheme.spacing(1),
    marginRight: CustomTheme.spacing(1)
  },
  footer: {
    display: "flex",
    justifyContent: "flex-end",
    marginTop: CustomTheme.spacing(6),
    padding: CustomTheme.spacing(1),
    borderTop: `1px solid ${CustomTheme.palette.grey[300]}`
  },
  vertDivider: {
    width: 1,
    height: 48
  },
  dragHandle: {
    display: "flex",
    paddingTop: CustomTheme.spacing(0.5),
    cursor: "move",
    alignItems: "center",
    justifyContent: "center",
    visibility: "hidden",
  },
  dragHandleActive: {
    visibility: "visible",
  },
  dragHandleHover: {
    visibility: "visible",
  },
  dragIcon: {
    opacity: "0.35",
    transform: "rotate(90deg)",
  }
};

export default Styles;
