// React
import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from "@material-ui/core/Radio";
import TextField from "@material-ui/core/TextField";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
// App custom
import { removeOption, setOptionValue } from "../../../redux/actions";
import CustomTheme from "../../../Theme";

const componentStyleOverrides = {
  optionContainer: {
    display: "flex",
    marginTop: CustomTheme.spacing(1)
  },
  optionName: {
    flex: "1 0 auto",
    "& .MuiInputBase-root": {
      marginTop: "10px"
    },
    "& .MuiInput-underline:before": {
      borderBottomColor: CustomTheme.palette.grey[300]
    },
    "& .MuiInputBase-input": {
      paddingTop: 0
    }
  },
  optionRemove: {
    paddingTop: 0,
    paddingBottom: 0,
    "&:hover": { backgroundColor: "transparent" }
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function Option(props) {
  const { type, value, parentId, optionId, removeOption, setOptionValue } = props;
  const classes = useStyles();

  function handleSet(event) {
    event.stopPropagation();
    if (event.target.value !== value) {
      setOptionValue(parentId, optionId, event.target.value);
    }
  }

  function handleRemove(event) {
    event.stopPropagation();
    removeOption(parentId, optionId);
  }

  return (
    <Box className={classes.optionContainer}>
      {type === "MultipleChoice" && <Radio color="default" disabled />}
      {type === "Checkboxes" && <Checkbox color="default" disabled />}
      <TextField
        defaultValue={value}
        classes={{ root: classes.optionName }}
        placeholder="Option 1"
        onBlur={handleSet}
      />
      <IconButton
        className={classes.optionRemove}
        aria-label="Remove option"
        onClick={handleRemove}
      >
        <CloseIcon />
      </IconButton>
    </Box>
  );
}

Option.propTypes = {
  type: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  parentId: PropTypes.string.isRequired,
  optionId: PropTypes.string.isRequired,
}

export default connect(
  null,
  { removeOption, setOptionValue }
)(Option);
