// React
import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import { getEmptyImage } from "react-dnd-html5-backend";
import clsx from "clsx";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import Input from "@material-ui/core/Input";
// App custom
import { setQuestionActive, setQuestionValue, moveQuestion } from "../../../redux/actions";
import ItemTypes from "../../../ItemTypes";
import CustomStyles from "../../Styles";
import questionTypes from "../../../questionTypes";
import Footer from "../../Footer";
import QuestionChanger from "../../QuestionChanger";
import DragHandle from "../../DragHandle";
import useQuestionDrag from "../../hooks/useQuestionDrag";
import useQuestionDrop from "../../hooks/useQuestionDrop";

function Text(props) {
  const {
    core,
    setQuestionActive,
    setQuestionValue,
    moveQuestion,
    isActive,
    width,
    label,
    index,
    id
  } = props;
  const inputIcon = props.inputIcon ? props.inputIcon : null;
  const ref = useRef(null);
  const componentStyleOverrides = {
    ...CustomStyles,
    input: {
      width: width
    }
  };
  const useStyles = makeStyles(componentStyleOverrides);
  const classes = useStyles();
  const [isHovered, setHovered] = useState(false);
  const customDrop = useQuestionDrop(ItemTypes.QUESTION, ref, index, moveQuestion);
  const customDrag = useQuestionDrag(ItemTypes.QUESTION, id, index, core.type);
  const questionTypeOptions = [
    questionTypes.ShortAnswer,
    questionTypes.Paragraph,
    questionTypes.Date,
  ];

  function handleChangeValue(name, value) {
    // Only change if the value is different.
    if (core[name] !== value) {
      setQuestionValue(core.id, name, value);
    }
  }

  function handleFocus(event) {
    event.stopPropagation();
    if (isActive !== true) {
      setQuestionActive(core.id);
    }
  }

  function handleHover(event) {
    event.stopPropagation();
    if (event.type === "mouseenter") {
      setHovered(true);
    } else {
      setHovered(false);
    }
  }

  customDrag.drag(customDrop.drop(ref));

  useEffect(() => {
    customDrag.preview(getEmptyImage(), { captureDraggingState: false });
  }, [customDrag]);

  return (
    <Paper
      ref={ref}
      id={core.id}
      className={clsx(
        classes.paper,
        isActive && classes.paperActive,
        customDrag.isDragging && classes.paperDragging
      )}
      onMouseDown={handleFocus}
      onMouseEnter={handleHover}
      onMouseLeave={handleHover}
      square={true}
      elevation={isActive === true ? 8 : 0}
    >
      <DragHandle ref={customDrag.drag} active={isActive} hover={isHovered} />
      <QuestionChanger
        id={core.id}
        type={core.type}
        options={questionTypeOptions}
      />
      <form className={classes.form} noValidate>
        <TextField
          name="question"
          defaultValue={core.question}
          id="question"
          classes={{ root: classes.title }}
          label="Question"
          onBlur={event =>
            handleChangeValue(event.target.name, event.target.value)
          }
          onFocus={handleFocus}
          required
          fullWidth
        />
        <TextField
          name="description"
          defaultValue={core.description}
          id="description"
          label="Description (optional)"
          onBlur={event =>
            handleChangeValue(event.target.name, event.target.value)
          }
          margin="normal"
          fullWidth
        />
        <Input
          defaultValue={label}
          className={classes.input}
          disabled
          inputProps={{
            "aria-label": label
          }}
        />
        {inputIcon !== null && (
          inputIcon
        )}
      </form>
      <Footer
        id={core.id}
        required={core.required}
        sensitive={core.sensitive}
      />
    </Paper>
  );
}

Text.propTypes = {
  isActive: PropTypes.bool.isRequired,
  core: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired
};

export default connect(
  null,
  { setQuestionActive, setQuestionValue, moveQuestion }
)(Text);
