// React
import React from "react";
// App custom
import Text from "./Shared/TextCore";
import InputIcon from "@material-ui/icons/Today";

function Date(props) {
  return (
    <Text
      {...props}
      label="Day, month, year"
      width="40%"
      inputIcon={<InputIcon style={{ opacity: 0.5, verticalAlign: "middle" }} />}
    />
  );
}

export default Date;
