// React
import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import { getEmptyImage } from "react-dnd-html5-backend";
import clsx from "clsx";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
// App custom
import {
  setQuestionActive,
  setQuestionValue,
  moveQuestion,
  bootstrapAdditional
} from "../../../redux/actions";
import { getAdditional } from "../../../redux/selectors";
import OptionsList from "./OptionsList";
import ItemTypes from "../../../ItemTypes";
import CustomStyles from "../../Styles";
import questionTypes from "../../../questionTypes";
import Footer from "../../Footer";
import QuestionChanger from "../../QuestionChanger";
import DragHandle from "../../DragHandle";
import useQuestionDrag from "../../hooks/useQuestionDrag";
import useQuestionDrop from "../../hooks/useQuestionDrop";

const componentStyleOverrides = {
  ...CustomStyles
};
const useStyles = makeStyles(componentStyleOverrides);

function Choice(props) {
  const {
    core,
    setQuestionActive,
    setQuestionValue,
    moveQuestion,
    isActive,
    bootstrapAdditional,
    additional,
    index,
    id
  } = props;
  const ref = useRef(null);
  const classes = useStyles();
  const [isHovered, setHovered] = useState(false);
  const customDrop = useQuestionDrop(
    ItemTypes.QUESTION,
    ref,
    index,
    moveQuestion
  );
  const customDrag = useQuestionDrag(ItemTypes.QUESTION, id, index, core.type);
  const questionTypeOptions = [
    questionTypes.MultipleChoice,
    questionTypes.Checkboxes
  ];

  function handleChangeValue(name, value) {
    // Only change if the value is different.
    if (core[name] !== value) {
      setQuestionValue(core.id, name, value);
    }
  }

  function handleFocus(event) {
    event.stopPropagation();
    if (isActive !== true) {
      setQuestionActive(core.id);
    }
  }

  function handleHover(event) {
    event.stopPropagation();
    if (event.type === "mouseenter") {
      setHovered(true);
    } else {
      setHovered(false);
    }
  }

  customDrag.drag(customDrop.drop(ref));

  useEffect(() => {
    customDrag.preview(getEmptyImage(), { captureDraggingState: false });
  }, [customDrag]);

  useEffect(() => {
    const keys = Object.keys(additional);

    if (keys.includes(id) !== true) {
      bootstrapAdditional(id);
    }

  }, [bootstrapAdditional, id, additional]);

  return (
    <Paper
      ref={ref}
      id={core.id}
      className={clsx(
        classes.paper,
        isActive && classes.paperActive,
        customDrag.isDragging && classes.paperDragging
      )}
      onMouseDown={handleFocus}
      onMouseEnter={handleHover}
      onMouseLeave={handleHover}
      square={true}
      elevation={isActive === true ? 8 : 0}
    >
      <DragHandle ref={customDrag.drag} active={isActive} hover={isHovered} />
      <QuestionChanger
        id={core.id}
        type={core.type}
        options={questionTypeOptions}
      />
      <form className={classes.form} noValidate>
        <TextField
          name="question"
          defaultValue={core.question}
          id="question"
          classes={{ root: classes.title }}
          label="Question"
          onBlur={event =>
            handleChangeValue(event.target.name, event.target.value)
          }
          onFocus={handleFocus}
          required
          fullWidth
        />
        <TextField
          name="description"
          defaultValue={core.description}
          id="description"
          label="Description (optional)"
          onBlur={event =>
            handleChangeValue(event.target.name, event.target.value)
          }
          margin="normal"
          fullWidth
        />
        <OptionsList parentId={id} optionType={core.type} />
      </form>
      <Footer
        id={id}
        required={core.required}
        sensitive={core.sensitive}
      />
    </Paper>
  );
}

const mapStateToProps = state => {
  const additional = getAdditional(state);

  return { additional };
};

Text.propTypes = {
  isActive: PropTypes.bool.isRequired,
  core: PropTypes.object.isRequired
};

export default connect(
  mapStateToProps,
  { setQuestionActive, setQuestionValue, moveQuestion, bootstrapAdditional }
)(Choice);
