// App Custom
import * as actionTypes from "../actionTypes";
import { uuid as genUuid } from "../../Utils/Math";
import { cloneArray } from "../../Utils/Array";

const questionObj = () => {
  return {
    type: null,
    id: null,
    question: "",
    description: "",
    required: false,
    sensitive: false,
  };
};
const initialQuestionId = genUuid();
const initialQuestionObj = {
  ...questionObj(),
  type: "ShortAnswer",
  id: initialQuestionId
};
const initialState = {
  active: initialQuestionId,
  list: [initialQuestionObj],
  count: 1
};

export default function(state = initialState, action) {
  switch (action.type) {
    case actionTypes.ADD_QUESTION: {
      const { type, id } = action.payload;
      const newQuestionObj = {
        ...questionObj(),
        type: type,
        id: id
      };

      return {
        ...state,
        list: [...state.list, newQuestionObj],
        count: ++state.count,
        active: id
      };
    }
    case actionTypes.REMOVE_QUESTION: {
      const { id } = action.payload;
      const listClone = cloneArray(state.list);
      const index = listClone.findIndex(question => question.id === id);

      if (index !== -1) {
        // Remove the question from the list
        listClone.splice(index, 1);

        return {
          ...state,
          list: [...listClone],
          count: --state.count,
          active: null
        };
      }

      return state;
    }
    case actionTypes.DUPLICATE_QUESTION: {
      const { id } = action.payload;
      const listClone = cloneArray(state.list);
      const index = listClone.findIndex(question => question.id === id);

      if (index !== -1) {
        const questionClone = Object.assign({}, listClone[index]);

        // Make adjustments to the cloned question obj
        questionClone.id = genUuid();
        // Insert the question clone into the list clone
        listClone.splice(index + 1, 0, questionClone);

        return {
          ...state,
          list: [...listClone],
          count: ++state.count,
          active: questionClone.id
        };
      }

      return state;
    }
    case actionTypes.SET_QUESTION_META: {
      const { id, key, value } = action.payload;
      const listClone = cloneArray(state.list);
      const index = listClone.findIndex(question => question.id === id);

      if (index !== -1) {
        const questionClone = Object.assign({}, listClone[index]);

        questionClone[key] = value;
        listClone[index] = questionClone;

        return {
          ...state,
          list: listClone
        };
      }

      return state;
    }
    case actionTypes.SET_QUESTION_VALUE: {
      // TODO: This is practically a clone of SET_QUESTION_META. If they remain the same, refactor into one reducer
      const { id, key, value } = action.payload;
      const listClone = cloneArray(state.list);
      const index = listClone.findIndex(question => question.id === id);

      if (index !== -1) {
        const questionClone = Object.assign({}, listClone[index]);

        questionClone[key] = value;
        listClone[index] = questionClone;

        return {
          ...state,
          list: listClone
        };
      }

      return state;
    }
    case actionTypes.SET_QUESTION_ACTIVE: {
      const { id } = action.payload;

      return {
        ...state,
        active: id
      };
    }
    case actionTypes.MOVE_QUESTION: {
      const { dragIndex, hoverIndex } = action.payload;
      const listClone = cloneArray(state.list);
      const dragItem = listClone[dragIndex];

      listClone.splice(dragIndex, 1);
      listClone.splice(hoverIndex, 0, dragItem);

      return {
        ...state,
        list: listClone
      };
    }
    default:
      return state;
  }
}
