// React
import React from "react";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Fab from "@material-ui/core/Fab";
import AddIcon from "@material-ui/icons/Add";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
// App custom
import CustomTheme from "./Theme";
import { addQuestion } from "./redux/actions";
import questionTypes from "./questionTypes";

const componentStyleOverrides = {
  fab: {
    position: "fixed",
    bottom: CustomTheme.spacing(2),
    right: CustomTheme.spacing(2)
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function QuestionTypeSelector(props) {
  const { addQuestion } = props;
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [selectedIndex, setSelectedIndex] = React.useState(0);
  const classes = useStyles();
  // Put the question types object into an array temporarily
  const questionTypesArray = () => {
    const array = [];
    for (const key of Object.keys(questionTypes)) {
      array.push(questionTypes[key]);
    }

    return array;
  };
  const renderOptions = questionTypesArray().map((option, index) => {
    const id = `QuestionTypeSelector-${option.type}`;

    return (
      <MenuItem
        key={option.name}
        id={id}
        selected={index === selectedIndex}
        onClick={event => handleMenuItemClick(event, index)}
      >
        <ListItemIcon>{option.icon}</ListItemIcon>
        {option.name}
      </MenuItem>
    );
  });

  function handleClickListItem(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleMenuItemClick(event, index) {
    event.stopPropagation();
    setSelectedIndex(index);
    setAnchorEl(null);
    const type = event.currentTarget.id.substring(21, event.currentTarget.id.length);
    addQuestion(type);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  return (
    <React.Fragment>
      <Fab
        className={classes.fab}
        color="primary"
        aria-label="Add"
        onClick={handleClickListItem}
      >
        <AddIcon />
      </Fab>
      <Menu
        id="lock-menu"
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {renderOptions}
      </Menu>
    </React.Fragment>
  );
}

export default connect(
  null,
  { addQuestion }
)(QuestionTypeSelector);
