import * as actionTypes from "./actionTypes";
import { uuid as genUuid } from "../Utils/Math";

export const addQuestion = type => ({
  type: actionTypes.ADD_QUESTION,
  payload: {
    type,
    id: genUuid()
  }
});

export const removeQuestion = id => ({
  type: actionTypes.REMOVE_QUESTION,
  payload: {
    id
  }
});

export const duplicateQuestion = id => ({
  type: actionTypes.DUPLICATE_QUESTION,
  payload: {
    id
  }
});

export const setQuestionMeta = (id, key, value) => ({
  type: actionTypes.SET_QUESTION_META,
  payload: {
    id,
    key,
    value
  }
});

export const setQuestionValue = (id, key, value) => ({
  type: actionTypes.SET_QUESTION_VALUE,
  payload: {
    id,
    key,
    value
  }
});

export const setQuestionActive = id => ({
  type: actionTypes.SET_QUESTION_ACTIVE,
  payload: {
    id
  }
});

export const moveQuestion = (dragIndex, hoverIndex) => ({
  type: actionTypes.MOVE_QUESTION,
  payload: {
    dragIndex,
    hoverIndex
  }
});

export const bootstrapAdditional = questionId => ({
  type: actionTypes.BOOTSTRAP_ADDITIONAL,
  payload: {
    questionId
  }
});

export const addOption = questionId => ({
  type: actionTypes.ADD_OPTION,
  payload: {
    questionId
  }
});

export const removeOption = (questionId, optionId) => ({
  type: actionTypes.REMOVE_OPTION,
  payload: {
    questionId,
    optionId
  }
});

export const setOptionValue = (questionId, optionId, newValue) => ({
  type: actionTypes.SET_OPTION_VALUE,
  payload: {
    questionId,
    optionId,
    newValue
  }
});

export const addOther = questionId => ({
  type: actionTypes.ADD_OTHER,
  payload: {
    questionId
  }
});

export const removeOther = questionId => ({
  type: actionTypes.REMOVE_OTHER,
  payload: {
    questionId
  }
});
