// React
import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from "@material-ui/core/Radio";
import TextField from "@material-ui/core/TextField";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
// App custom
import { removeOther } from "../../../redux/actions";
import CustomTheme from "../../../Theme";

const componentStyleOverrides = {
  otherContainer: {
    display: "flex",
    marginTop: CustomTheme.spacing(1)
  },
  otherName: {
    flex: "1 0 auto",
    "& .MuiInputBase-root": {
      marginTop: "10px"
    },
    "& .MuiInput-underline:before": {
      borderBottomColor: CustomTheme.palette.grey[300]
    },
    "& .MuiInputBase-input": {
      paddingTop: 0
    }
  },
  otherButton: {
    paddingTop: 0,
    paddingBottom: 0,
    "&:hover": { backgroundColor: "transparent" }
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function Other(props) {
  const { type, parentId, removeOther } = props;
  const classes = useStyles();

  function handleRemoveOther(event) {
    event.stopPropagation();
    removeOther(parentId);
  }

  return (
    <Box className={classes.otherContainer}>
      {type === "MultipleChoice" && <Radio color="default" disabled />}
      {type === "Checkboxes" && <Checkbox color="default" disabled />}
      <TextField
        value="Other..."
        classes={{ root: classes.otherName }}
        placeholder="Other..."
      />
      <IconButton
        className={classes.otherButton}
        aria-label="Remove option"
        onClick={handleRemoveOther}
      >
        <CloseIcon />
      </IconButton>
    </Box>
  );
}

Other.propTypes = {
  type: PropTypes.string.isRequired,
  parentId: PropTypes.string.isRequired,
}

export default connect(
  null,
  { removeOther }
)(Other);
