// React & Material UI
import React, { forwardRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
// Material UI components
import DragIcon from "@material-ui/icons/DragIndicator";
// App Custom
import CustomStyles from "./Styles";

const useStyles = makeStyles(CustomStyles);

function DragHandle(props, ref) {
  const {active, hover} = props;
  const classes = useStyles();

  return (
    <div ref={ref} className={
      clsx(
        classes.dragHandle,
        hover && classes.dragHandleHover,
        active && classes.dragHandleActive
      )
    }>
      <DragIcon className={classes.dragIcon} />
    </div>
  );
}

// Fix for: Function components cannot be given refs. Attempts to access this ref will fail. Did you mean to use React.forwardRef()
// https://reactjs.org/docs/forwarding-refs.html
export default forwardRef(DragHandle);