// React & Material UI
import React, { useState } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import DownIcon from "@material-ui/icons/ArrowDropDown";
// App custom
import { setQuestionMeta } from "../redux/actions";
import CustomTheme from "../Theme";

const changerTargetWidth = 200;
const componentStyleOverrides = {
  container: {
    display: "flex",
    justifyContent: "flex-end"
  },
  button: {
    "& .MuiButton-label": {
      display: "flex",
      width: `${changerTargetWidth}px`,
      justifyContent: "flex-start"
    }
  },
  downIcon: {
    marginLeft: "auto"
  },
  menu: {
    width: `${changerTargetWidth + CustomTheme.spacing(4) + 2}px`
  },
  menuItem: {
    fontSize: CustomTheme.typography.pxToRem(14),
    fontWeight: CustomTheme.typography.fontWeightMedium,
    textTransform: "uppercase"
  },
  listItem: {
    minWidth: "auto"
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function QuestionTypeChanger(props) {
  const { id, type, setQuestionMeta, options } = props;
  const classes = useStyles();
  const startIndex = findTypeIndex(type);
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedIndex, setSelectedIndex] = useState(startIndex);
  const renderOptions = options.map((option, index) => {
    return (
      <MenuItem
        key={option.name}
        selected={index === selectedIndex}
        onClick={event => handleMenuItemClick(event, index)}
        classes={{ root: classes.menuItem }}
      >
        <ListItemIcon classes={{ root: classes.listItem }}>
          {option.icon}
        </ListItemIcon>
        {option.name}
      </MenuItem>
    );
  });

  function findTypeIndex(needle) {
    return options.findIndex(option => option.type === needle);
  }

  function handleClickListItem(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleMenuItemClick(event, index) {
    event.stopPropagation();
    setSelectedIndex(index);
    setAnchorEl(null);
    if (type !== options[index].type) {
      setQuestionMeta(id, "type", options[index].type);
    }
  }

  function handleClose() {
    setAnchorEl(null);
  }

  return (
    <div className={classes.container}>
      <Button
        className={classes.button}
        variant="outlined"
        aria-owns={anchorEl ? "simple-menu" : undefined}
        aria-haspopup="true"
        onClick={handleClickListItem}
      >
        {options[selectedIndex].icon}
        {options[selectedIndex].name}
        <DownIcon className={classes.downIcon} />
      </Button>
      <Menu
        id="simple-menu"
        classes={{ paper: classes.menu }}
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {renderOptions}
      </Menu>
    </div>
  );
}

QuestionTypeChanger.propTypes = {
  id: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  options: PropTypes.array.isRequired
};

export default connect(
  null,
  { setQuestionMeta }
)(QuestionTypeChanger);
