// React
import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from "@material-ui/core/Radio";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
// App custom
import { addOption, addOther } from "../../../redux/actions";
import CustomTheme from "../../../Theme";

const componentStyleOverrides = {
  optionContainer: {
    display: "flex",
    marginTop: CustomTheme.spacing(1)
  },
  optionAdd: {
    "& .MuiInputBase-root": {
      marginTop: "10px"
    },
    "& .MuiInput-underline:before": {
      borderBottomColor: CustomTheme.palette.grey[300]
    },
    "& .MuiInputBase-input": {
      paddingTop: 0,
      color: CustomTheme.palette.grey[500]
    }
  },
  textInline: {
    alignSelf: "center",
    margin: "0 10px"
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function AddOther(props) {
  const { type, hasOther, parentId, addOption, addOther } = props;
  const classes = useStyles();

  function handleAddOption(event) {
    event.stopPropagation();
    addOption(parentId);
  }

  function handleAddOther(event) {
    event.stopPropagation();
    addOther(parentId);
  }

  return (
    <Box className={classes.optionContainer}>
      {type === "MultipleChoice" && <Radio color="default" disabled />}
      {type === "Checkboxes" && <Checkbox color="default" disabled />}
      <TextField
        defaultValue="Add option"
        classes={{ root: classes.optionAdd }}
        onClick={handleAddOption}
        InputProps={{
          readOnly: true
        }}
      />
      {hasOther === false && (
        <Typography className={classes.textInline}>or</Typography>
      )}
      {hasOther === false && (
        <Button color="primary" className={classes.button} onClick={handleAddOther}>
          Add 'other'
        </Button>
      )}
    </Box>
  );
}

AddOther.propTypes = {
  type: PropTypes.string.isRequired,
  hasOther: PropTypes.bool.isRequired,
  parentId: PropTypes.string.isRequired,
};

export default connect(
  null,
  { addOption, addOther }
)(AddOther);
