export const getQuestionsState = store => store.questions;

export const getQuestions = store => getQuestionsState(store) ? getQuestionsState(store) : {};

export const getActive = store => {
  const questions = getQuestionsState(store);

  if (questions) {
    return questions.active;
  } else {
    return null;
  }
};

export const getQuestionCount = store => getQuestionsState(store) ? getQuestionsState(store).count : null;

export const getAdditionalState = store => store.additional;

export const getAdditional = store => getAdditionalState(store) ? getAdditionalState(store) : {};

export const getOptions = (store, questionId) => {
  const additional = getAdditionalState(store);
  const keys = Object.keys(additional);

  if (keys.includes(questionId) === true) {
    return additional[questionId].options;
  } else {
    return null;
  }
};
