// App Custom
import * as actionTypes from "../actionTypes";
import { uuid as genUuid } from "../../Utils/Math";
import { cloneArray } from "../../Utils/Array";

const additionalObj = () => {
  return {
    options: [
      {
        value: `Option 1`,
        id: genUuid()
      }
    ],
    hasOther: false
  };
};
const generateOption = n => {
  return {
    value: `Option ${n}`,
    id: genUuid()
  };
};

export default function(state = {}, action) {
  switch (action.type) {
    case actionTypes.BOOTSTRAP_ADDITIONAL: {
      const { questionId } = action.payload;

      return {
        ...state,
        [questionId]: Object.assign({}, additionalObj())
      };
    }
    case actionTypes.ADD_OPTION: {
      const { questionId } = action.payload;
      const newAnchor = state[questionId].options.length + 1;
      const newOption = generateOption(newAnchor);
      const optionsClone = cloneArray(state[questionId].options);
      const questionClone = Object.assign({}, state[questionId]);

      optionsClone.push(newOption);
      questionClone.options = optionsClone;

      return {
        ...state,
        [questionId]: questionClone
      };
    }
    case actionTypes.REMOVE_OPTION: {
      const { questionId, optionId } = action.payload;
      const optionsClone = cloneArray(state[questionId].options);
      const index = optionsClone.findIndex(option => option.id === optionId);

      if (state[questionId].options.length > 1 && index !== -1) {
        const questionClone = Object.assign({}, state[questionId]);

        optionsClone.splice(index, 1);
        questionClone.options = optionsClone;

        return {
          ...state,
          [questionId]: questionClone
        };
      }
      return state;
    }
    case actionTypes.SET_OPTION_VALUE: {
      const { questionId, optionId, newValue } = action.payload;
      const optionsClone = cloneArray(state[questionId].options);
      const index = optionsClone.findIndex(option => option.id === optionId);

      if (index !== -1) {
        const questionClone = Object.assign({}, state[questionId]);

        optionsClone[index].value = newValue;
        questionClone.options = optionsClone;

        return {
          ...state,
          [questionId]: questionClone
        };
      }

      return state;
    }
    // TODO: refactor add / remove other to be a toggle like func
    case actionTypes.ADD_OTHER: {
      const { questionId } = action.payload;
      const questionClone = Object.assign({}, state[questionId]);

      questionClone.hasOther = true;

      return {
        ...state,
        [questionId]: questionClone
      };
    }
    case actionTypes.REMOVE_OTHER: {
      const { questionId } = action.payload;
      const questionClone = Object.assign({}, state[questionId]);

      questionClone.hasOther = false;

      return {
        ...state,
        [questionId]: questionClone
      };
    }
    default:
      return state;
  }
}
