// React
import React from "react";
import { Provider } from "react-redux";
// Material UI
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
// App custom
import { setQuestionActive } from "./redux/actions";
import QuestionTypeSelector from "./QuestionTypeSelector";
import QuestionList from "./QuestionList";
import CustomDragLayer from "./CustomDragLayer";
import CustomTheme from "./Theme";
import store from "./redux/store";

const componentStyleOverrides = {
  formBuilder: {
    paddingTop: CustomTheme.spacing(8),
    paddingBottom: CustomTheme.spacing(8),
  },
  container: {
    padding: 0,
    boxShadow: CustomTheme.shadows[1],
  }
};
const useStyles = makeStyles(componentStyleOverrides);

function FormBuilder() {
  const classes = useStyles();
  const handleOutsideClick = event => {
    event.preventDefault();
    const state = store.getState();
    if (state.questions.count > 1) {
      store.dispatch(setQuestionActive(null));
    }
  };

  return (
    <Provider store={store}>
      <div id="FormBuilder" className={classes.formBuilder} onMouseDown={handleOutsideClick}>
        <Container className={classes.container} maxWidth="md">
          <CustomDragLayer />
          <QuestionList />
        </Container>
        <QuestionTypeSelector />
      </div>
    </Provider>
  );
}

export default FormBuilder;
