// React
import React from "react";
// Material UI
import ShortTextIcon from "@material-ui/icons/ShortText";
import NotesIcon from "@material-ui/icons/Notes";
import RadioButtonIcon from "@material-ui/icons/RadioButtonChecked";
import CheckboxIcon from "@material-ui/icons/CheckBox";
import DateIcon from "@material-ui/icons/Today";
// App custom
import CustomTheme from "./Theme";
import ShortAnswer from "./QuestionTypes/Text/ShortAnswer";
import Paragraph from "./QuestionTypes/Text/Paragraph";
import DateAnswer from "./QuestionTypes/Text/Date";
import MultipleChoice from "./QuestionTypes/Choice/MultipleChoice";
import Checkboxes from "./QuestionTypes/Choice/Checkboxes";

const componentStyleOverrides = {
  icon: {
    marginRight: CustomTheme.spacing(2),
  },
};
const questionTypes = {
  ShortAnswer: {
    name: "Short Answer",
    type: "ShortAnswer",
    icon: <ShortTextIcon style={componentStyleOverrides.icon} />,
    component: (props) => <ShortAnswer {...props} />,
  },
  Paragraph: {
    name: "Paragraph",
    type: "Paragraph",
    icon: <NotesIcon style={componentStyleOverrides.icon} />,
    component: (props) => <Paragraph {...props} />,
  },
  Date: {
    name: "Date",
    type: "Date",
    icon: <DateIcon style={componentStyleOverrides.icon} />,
    component: (props) => <DateAnswer {...props} />,
  },
  MultipleChoice: {
    name: "Multiple Choice",
    type: "MultipleChoice",
    icon: <RadioButtonIcon style={componentStyleOverrides.icon} />,
    component: (props) => <MultipleChoice {...props} />,
  },
  Checkboxes: {
    name: "Checkboxes",
    type: "Checkboxes",
    icon: <CheckboxIcon style={componentStyleOverrides.icon} />,
    component: (props) => <Checkboxes {...props} />,
  }
};

export default questionTypes;
